.App {
  font-size: 18px;
  color: white;
  display: flex;
  flex-direction: column;
  }

  .App-header {
    font-size: 24px;
    margin-top: 30px;
  text-align: center;
}

.quiz {
  margin: 0 40px;
}

.question {
  margin-bottom: 10px;
}

.question input {
  padding: 8px 12px;
  border: 1px solid white;
  border-radius: 5px;
  background: none;
  color: white;
  min-width: 300px;
}

button {
  font-size: 16px;
  background: none;
  outline: none;
  border: 2px solid white;
  border-radius: 4px;
  color: white;
  padding: 8px 15px;
  margin-top: 10px;
  cursor: pointer;
}

button:hover {
  background: rgba(255, 255, 255, .1)
}
